@import "styles/variables/index";
@import "styles/mixins";

@keyframes pulse {
  0% {
    transform: scale(0);
    opacity: 0.9;
  }

  100% {
    transform: scale(1);
    opacity: 0;
  }
}

@keyframes colors {
  0% {
    background-color: yellow;
  }

  50% {
    background-color: yellow;
  }

  60% {
    background-color: blue;
  }

  100% {
    background-color: blue;
  }
}

.point {
  --point-offset-x: 0;
  --point-offset-y: 4rem;
  --point-size: 6px;

  &::before {
    content: "";
    position: absolute;
    top: calc(var(--point-size) / 2 + var(--point-offset-y) / 2);
    left: calc(2rem - 2rem);
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    transform-origin: center;
    animation: pulse 3s infinite cubic-bezier(0.215, 0.61, 0.355, 1);
    background-image: linear-gradient(to bottom, #0057B7, #FFDD00 60%);
  }

  &::after {
    content: "";
    position: absolute;
    top: var(--point-offset-y);
    left: calc(2rem - var(--point-size) / 2);
    width: var(--point-size);
    height: var(--point-size);
    background-color: color(darkGray);
    border-radius: 50%;
  }
}

.pointLeft {
  &::before {
    right: calc(-1.5rem - 3rem - 2px);
    left: auto;
    top: calc(50% - 2rem);
  }

  &::after {
    right: -3rem;
    left: auto;
    top: calc(50% - var(--point-size) / 2);
  }
}

.name {
  @include fontLabel;

  display: inline-block;
  width: 9rem;
}

@media (max-width: $adaptiveMax) {
  .pointLeft {
    &::before {
      right: calc(-1.5rem - 2rem - 2px);
      left: auto;
      top: calc(50% - 2rem);
    }

    &::after {
      right: -2rem;
      left: auto;
      top: calc(50% - var(--point-size) / 2);
    }
  }
}