@import "styles/mixins";
@import "styles/variables/index";

.wrapper {
  position: relative;
  margin-top: 16.4rem;
  padding-bottom: 50rem;
}

.container {
  padding-left: 92.3rem;
}

.description {
  @include fontRegular(color(darkGray));

  margin: 2.4rem 0 0 0;
}

.image1,
.frame {
  position: absolute;
  object-fit: cover;
}

.image1 {
  top: 0;
  left: 0;
  width: calc(705 / 1440 * 100%);
  height: 41rem;
}

.frame {
  display: flex;
  flex-direction: column;
  top: 28.4rem;
  left: 22.1rem;
  width: calc(623 / 1440 * 100%);
  z-index: 1;
}

.image2 {
  width: 100%;
  height: 36.2rem;
  object-fit: cover;
}

.link,
.label {
  @include fontSmall;

  display: inline-block;
  margin-top: 0.8rem;
}

.link {
  text-decoration: underline;

  &:hover {
    text-decoration: none;
  }
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    margin-top: 8rem;
    padding-bottom: 47.1rem;
  }

  .container {
    padding-left: 1.7rem;
  }

  .image1 {
    top: auto;
    bottom: 25.4rem;
    left: 0;
    width: 32.7rem;
    height: 19rem;
  }

  .frame {
    top: auto;
    bottom: 10.8rem;
    right: 0;
    left:auto;
    width: 32.7rem;
    height: 19rem;
  }

  .image2 {
    width: 100%;
    height: 36.2rem;
  }
}