@import "../../../styles/mixins";

.container {
  display: flex;
  align-items: center;
  padding-top: 2.5rem;
  padding-bottom: 2.5rem;

  .logo {
    height: 6rem;
  }

  .nav {
    margin-left: 8.7rem;
  }

  .lang {
    margin-left: 7rem;
  }

  .social {
    margin-left: 3rem
  }

  .support {
    margin-left: auto;
  }
}

@media (max-width: $adaptiveMax) {
  .container {
    .nav,
    .lang,
    .social {
      display: none;
    }

    .logo {
      height: 4rem;
    }
  }
}