@import "styles/mixins";
@import "styles/variables/index";

@keyframes float {
  0% {
    top: 1rem;
    transform: rotate(0);
  }

  50% {
    top: 10rem;
    left: -10rem;
    transform: rotate(25deg);
  }

  100% {
    top: 1rem;
    transform: rotate(0);
  }
}

.wrapper {
  position: relative;
  padding-bottom: 18rem;
  overflow: hidden;

  &::before {
    content: "";
    position: absolute;
    top: 1rem;
    left: -3rem;
    width: 33.6rem;
    height: 55.1rem;
    background-image: url("../../../assets/images/fund_shape.png");
    background-size: cover;
    animation: float 30s ease infinite;
  }
}

.frame {
  position: relative;
  padding-top: 10rem;
  padding-right: 56.6rem;
}

.description,
.label {
  @include fontRegular(color(darkGray), 400);
}

.description {
  margin: 3.6rem 0 0 0;
  padding-right: 15rem;
}

.label {
  position: relative;
  display: inline-block;
  margin-top: 7rem;
  margin-right: 20rem;

  &::before {
    content: "";
    position: absolute;
    top: calc(50% - 2rem / 2);
    right: -1rem;
    width: 4rem;
    height: 2rem;
    background-image: url("../../../assets/icons/icon-arrow.svg");
    background-size: cover;
  }
}

.map {
  position: absolute;
  top: 0;
  right: -5rem;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    padding-bottom: 41rem;
    overflow: hidden;
  }

  .frame {
    padding-top: 3.4rem;
    padding-right: 0;
  }

  .description,
  .label {
    margin: 1.6rem 0 0 0;
    padding-right: 0;
  }

  .label {
    font-size: 1.5rem;
    line-height: 2.25rem;
    letter-spacing: 0.05rem;

    &::before {
      top: auto;
      bottom: -3rem;
      right: auto;
      left: 0;
    }
  }

  .map {
    top: 7rem;
    right: -32rem;
  }
}