@import "styles/variables/index";
@import "styles/mixins";

.wrapper {
  width: 90rem;
}

.name {
  text-align: center;
}

.description {
  @include fontRegular(color(black));

  margin-top: 2.2rem;

  p {
    margin: 0;
  }
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    width: 100%;
  }

  .description {
    margin-top: 0.8rem;
  }
}