@import "styles/variables/index";
@import "styles/mixins";

.wrapper {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 20rem;
  grid-column-gap: 2.4rem;
  margin: 0;
  padding-top: 3.2rem;

  &::before {
    @include gradientBg;

    content: "";
    position: absolute;
    bottom: 0;
    left: -4rem;
    width: calc(100% + 2 * 4rem);
    height: 1px;
  }
}

.label {
  @include fontRegular(color(black));
}

.list {
  @include resetList;

  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-gap: 1.6rem;
  margin-top: 3.2rem;
}

.iconToken {
  width: 100%;
}

.wallet {
  @include fontRegular(color(black));

  position: relative;
  display: flex;
  align-items: center;
  padding: 0 1.6rem;
  height: 6rem;
  background-color: rgba(0, 0, 0, 0.05);
  transition-duration: $transitionDuration;
  transition-property: background-color;
}

.walletCopied {
  background-color: lightgreen;
}

.buttonCopy {
  @include resetButton;

  margin-left: auto;
  width: 3.25rem;
  height: 3.25rem;
}

.iconCopy {
  width: 100%;
  fill: color(black);
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    display: flex;
    flex-direction: column;
    grid-column-gap: 0;
    margin: 0;
    padding-top: 2.4rem;
    padding-bottom: 2.4rem;
  }

  .qr {
    margin: 2.4rem auto 0 auto;
    width: 12rem;
  }

  .list {
    grid-template-columns: repeat(auto-fit, 2rem);
    grid-gap: 0.8rem;
    margin-top: 1.6rem;
  }

  .wallet {
    width: 100%;
    height: 4rem;
    font-size: 1rem;
  }

  .buttonCopy {
    width: 2rem;
    height: 2rem;
  }
}