@import "styles/mixins";
@import "styles/variables/index";

@keyframes float {
  0% {
    top: 1rem;
    transform: rotate(0);
  }

  50% {
    top: 10rem;
    right: -10rem;
    transform: rotate(25deg);
  }

  100% {
    top: 1rem;
    transform: rotate(0);
  }
}

.wrapper {
  position: relative;
  overflow: hidden;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 34.1rem;
    height: 61rem;
    background-image: url("../../../assets/images/fund_details_shape.png");
    background-size: cover;
    animation: float 10s ease infinite;
  }
}

.content {
  @include fontRegular(color(darkGray));

  margin-top: 3rem;
  width: 70rem;

  p {
    margin: 0;

    &:not(:first-child) {
      margin-top: 2.4rem;
    }
  }
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    overflow: hidden;

    &::before {
      top: 15rem;
      right: -10rem;
      width: 34.1rem;
      height: 61rem;
    }
  }

  .content {
    margin-top: 3.2rem;
    width: auto;

    p {
      margin: 0;

      &:not(:first-child) {
        margin-top: 0.8rem;
      }
    }
  }
}