@import "styles/variables/index";
@import "styles/mixins";

.wrapper {
  padding-top: 2.6rem;
  padding-bottom: 2.8rem;
  background-color: color(black);
  border-top: 1px solid;
  border-image: linear-gradient(94.98deg, rgba(0, 92, 255, 0.5) 1.39%, rgba(255, 255, 0, 0.3) 97.12%) 1;
}

.container {
  display: flex;
  align-items: center;
}

.logo {
  width: 13rem;
}

.navigation {
  margin-left: 28rem;
}

.lang {
  margin-left: 9rem;
}

.social {
  margin-left: auto;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    padding-top: 0.8rem;
    padding-bottom: 1.6rem;
  }

  .container {
    display: grid;
    grid-template-columns: 6.5rem 1fr 7.2rem;
    grid-column-gap: 2.3rem;
    align-items: flex-start;
  }

  .logo {
    grid-column: 1 / 2;
    grid-row: 1 / 2;
    margin-top: 0.8rem;
    width: 100%;
  }

  .navigation {
    grid-column: 2 / 3;
    margin-left: 0;
  }

  .lang {
    grid-column: 3 / 4;
    grid-row: 1 / 2;
    margin-left: auto;
  }

  .social {
    margin-top: auto;
    grid-column: 3 / 4;
    grid-row: 1 / 2;
  }
}