@import "styles/mixins";
@import "styles/variables/index";

.button {
  @include button;

  position: relative;
  width: 21.8rem;
  height: 6rem;

  &:hover {
    .iconBroken2 {
      transform: translateY(4rem);
    }

    .iconCity {
      transform: translateY(-5rem);
    }
  }
}

.buttonActive {
  .iconBroken2 {
    transform: translateY(4rem);
  }

  .iconCity {
    transform: translateY(-5rem);
  }
}

.iconCity {
  position: absolute;
  bottom: -5rem;
  right: 0.5rem;
  width: 3.5rem;
  stroke: white;
  fill: color(white);
  transition-duration: $transitionDuration;
}

.iconBroken2 {
  position: absolute;
  bottom: -0.5rem;
  right: 0.4rem;
  width: 3.5rem;
  fill: color(white);
  transition-duration: $transitionDuration;
}

@media (max-width: $adaptiveMax) {
  .button {
    width: 12rem;
    height: 4.4rem;
  }
}