@import "styles/variables/index";
@import "styles/mixins";

.wrapper {
  padding-top: 5.6rem;
  padding-bottom: 5.6rem;
  background-color: color(lightBlue);
}

.heading {
  text-align: center;
}

.form {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  grid-gap: 4rem 3rem;
  margin-top: 3.7rem;
  padding: 0 11rem;
}

.input {
  @include input;
}

.inputWide {
  grid-column: 1 / -1;
}

.button {
  @include button;

  margin: 4.8rem auto 0 auto;
  width: 41rem;
  height: 7rem;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    padding-top: 3.2rem;
    padding-bottom: 3.2rem;
  }

  .form {
    grid-template-columns: 100%;
    grid-gap: 2.4rem 0;
    margin-top: 3.2rem;
    padding: 0;
  }

  .button {
    margin: 4rem auto 0 auto;
    width: 100%;
    height: 5.6rem;
  }
}