@import "styles/variables/index";

.map {
  width: 62.1rem;
  height: 119.3rem;
}

.city {
  position: absolute;
}

.Palyanytsia {
  top: 43rem;
  left: -2rem;
}

.Uzhgorod {
  top: 46rem;
  left: -4rem;
}

.Khust {
  top: 48rem;
  left: 12rem;
}

.Tyachiv {
  top: 52rem;
  left: 17rem;
}

@media (max-width: $adaptiveMax) {
  .map {
    transform: scale(0.8) rotate(10deg);
  }

  .Palyanytsia {
    top: 43rem;
    left: 5.5rem;
  }

  .Uzhgorod {
    top: 46rem;
    left: 3.5rem;
  }

  .Khust {
    top: 48rem;
    left: 17rem;
  }

  .Tyachiv {
    top: 52rem;
    left: 22rem;
  }
}
