@import "styles/variables/index";
@import "styles/mixins";

.wrapper {
  padding-top: 15rem;
  padding-bottom: 15rem;
}

.list {
  @include resetList;

  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 2.9rem;
  margin-top: 5.4rem;
}

.item {
  position: relative;

  padding: 2.4rem 2.4rem 0 2.4rem;

  &::before {
    @include gradientBg;

    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 2px;
  }
}

.duration {
  @include h2(color(darkGray), 400);

  text-align: center;
}

.counter {
  @include h0;

  margin-right: 1.8rem;
}

.description {
  @include fontRegular(color(darkGray));

  margin: 0;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    padding-top: 8rem;
    padding-bottom: 8rem;
  }

  .list {
    grid-template-columns: 100%;
    grid-gap: 2.9rem;
    margin-top: 2.2rem;
  }

  .item {
    padding: 0.8rem 0;
  }

  .duration {
    font-size: 2.4rem;
    line-height: 3.1rem;
    text-align: left;
  }

  .counter {
    margin-right: 0.8rem;
  }

  .description {
    margin: 0.8rem 0 0 0;
  }
}