@import "styles/mixins";

.wrapper {
  padding-top: 15rem;
  padding-bottom: 20rem;
}

.list {
  @include resetList;

  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  grid-gap: 6rem;
  margin-top: 5.6rem;
}

.button {
  @include resetButton;

  display: block;
  width: 100%;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
}

.photo {
  width: 100%;
  height: 26.6rem;
  object-fit: cover;
}

.name {
  margin-top: 2.4rem;
}

.rank {
  @include fontRegular(color(darkGray));

  display: inline-block;
  margin-top: 0.8rem;
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    padding-top: 8rem;
    padding-bottom: 8rem;
  }

  .list {
    @include resetList;

    grid-template-columns: 100%;
    grid-gap: 4rem;
    margin-top: 1.8rem;
  }

  .photo {
    display: block;
    margin: 0 auto;
    width: 19.3rem;
    height: 19.3rem;
  }

  .name {
    margin-top: 0.8rem;
  }

  .rank {
    font-size: 1.5rem;
    line-height: 2.1rem;
  }
}