@import "styles/mixins";
@import "styles/variables/index";

.wrapper {
  margin-top: 15rem;
}

.container {
  display: flex;
  flex-direction: column;
}

.list {
  @include resetList;

  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 5.6rem 3rem;
  margin-top: 5rem;
}

.item {
  @include fontRegular(color(darkGray));

  position: relative;
  padding-top: 2.4rem;
  padding-left: 2.4rem;
  padding-right: 5rem;

  &::before {
    @include gradientBg;

    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 2px;
  }
}

.link {
  @include fontRegular(color(darkGray));

  margin-top: 4.8rem;
  margin-right: 4.2rem;
  margin-left: auto;
  letter-spacing: 0.05rem;
  text-decoration: underline;

  &:hover,
  &:active {
    text-decoration: none;
  }
}

@media (max-width: $adaptiveMax) {
  .wrapper {
    margin-top: 8rem;
  }

  .list {
    grid-template-columns: 100%;
    grid-gap: 2.4rem 0;
    margin-top: 3.2rem;
  }

  .item {
    padding-top: 1rem;
    padding-left: 1.6rem;
    padding-right: 1.6rem;
  }

  .link {
    margin-top: 2.4rem;
    margin-right: auto;
    margin-left: 0;
    font-size: 1.5rem;
    line-height: 3.6rem;
  }
}