@import "styles/variables/index";

// font mixins
@mixin h0 ($color: color(darkGray)) {
  font-family: "Futura", Arial, sans-serif;
  font-size: 6rem;
  font-weight: 600;
  line-height: 5.2rem;
  color: $color;
}

@mixin h1 ($color: color(darkGray)) {
  font-family: "Futura", Arial, sans-serif;
  font-size: 4.4rem;
  font-weight: 500;
  line-height: 6.2rem;
  color: $color;

  @media (max-width: $adaptiveMax) {
    font-size: 2.4rem;
    line-height: 3.1rem;
  }
}

@mixin h2 ($color: color(black), $weight: 500) {
  font-family: "Futura", Arial, sans-serif;
  font-size: 4rem;
  font-weight: $weight;
  line-height: 5.2rem;
  color: $color;

  @media (max-width: $adaptiveMax) {
    font-size: 2.1rem;
    line-height: 2.7rem;
  }
}

@mixin h3 ($color: color(darkGray)) {
  font-family: "Futura", Arial, sans-serif;
  font-size: 3.2rem;
  font-weight: 500;
  line-height: 5.2rem;
  color: $color;

  @media (max-width: $adaptiveMax) {
    font-size: 2.1rem;
    line-height: 2.7rem;
  }
}

@mixin h4 ($color: color(darkGray)) {
  font-family: "Futura", Arial, sans-serif;
  font-size: 2.4rem;
  font-weight: 600;
  line-height: 3.6rem;
  letter-spacing: 0.05rem;
  color: $color;

  @media (max-width: $adaptiveMax) {
    font-size: 1.7rem;
    line-height: 2.2rem;
  }
}

@mixin fontRegular ($color: color(white), $weight: 400) {
  font-family: "BeauSans", Arial, sans-serif;
  font-size: 2.4rem;
  font-weight: $weight;
  line-height: 3.6rem;
  letter-spacing: -0.1rem;
  color: $color;

  @media (max-width: $adaptiveMax) {
    font-size: 1.7rem;
    line-height: 2.5rem;
  }
}

@mixin fontLabel ($color: color(black)) {
  font-family: "BeauSans", Arial, sans-serif;
  font-size: 2rem;
  font-weight: 400;
  line-height: 2.4rem;
  color: $color;

  @media (max-width: $adaptiveMax) {
    font-size: 1.5rem;
    line-height: 2.25rem;
  }
}

@mixin fontSmall ($color: color(darkGray)) {
  font-family: "BeauSans", Arial, sans-serif;
  font-size: 1.8rem;
  font-weight: 400;
  line-height: 2.7rem;
  letter-spacing: 0.05rem;
  color: $color;

  @media (max-width: $adaptiveMax) {
    font-size: 1.2rem;
    line-height: 1.8rem;
  }
}

@mixin fontNavigation ($color) {
  font-family: "BeauSans", Arial, sans-serif;
  font-size: 1.2rem;
  font-weight: 500;
  line-height: 2.2rem;
  letter-spacing: 0.15rem;
  color: $color;
}

// reset mixins
@mixin resetButton {
  padding: 0;
  font-size: 0;
  background-color: transparent;
  border: none;
  box-shadow: none;
  border-radius: 0;
  cursor: pointer;
}

@mixin resetList {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin resetFieldset {
  margin: 0;
  padding: 0;
  border: none;
}


// style mixins
@mixin visuallyHidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
  font-size: 0;
}

@mixin inputDefault {
  font-size: 1.4rem;
  color: blue;
  height: 3rem;
  border: 1px solid pink;
  border-radius: 0;
}

@mixin input {
  @include fontRegular(color(darkGray));

  padding: 0.8rem 0;
  line-height: 2.9rem;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid color(darkGray);
}

@mixin button {
  @include resetButton;
  @include fontRegular(color(white));

  display: flex;
  justify-content: center;
  align-items: center;
  background-color: color(black);
  overflow: hidden;
}

@keyframes gradient {
  0% {
    background-position: 50% 0;
  }
  50% {
    background-position: -50% 0;
  }
  100% {
    background-position: 50% 0;
  }
}

@mixin gradientBg {
  background-image: linear-gradient(to right, rgba(255, 255, 0, 0.365) 15%, rgba(0, 92, 255, 0.5) 50%, rgba(255, 255, 0, 0.365) 85%);
  background-size: 200% 400%;
  animation: gradient 10s ease infinite;
}